<template>
  <div class="hello">   
    <el-form ref="form" :model="form" label-width="80px">
      <el-col :span="6">
        <el-form-item label="优先设置">
          <el-select v-model="form.priority1" placeholder="请选择计算偏好">
            <el-option v-for="item in prioritys1" :value="item.name" :key="item.Id"             
               :label="item.name"></el-option>
        </el-select>
      </el-form-item>
     </el-col>
     <el-col :span="6">
          <el-select v-model="form.priority2" placeholder="请选择计算偏好">
            <el-option v-for="item in prioritys2" :value="item.name" :key="item.Id"             
               :label="item.name"></el-option>
        </el-select>
        </el-col>
        <el-col :span="6">
          <el-select v-model="form.priority3" placeholder="请选择计算偏好">
            <el-option v-for="item in prioritys3" :value="item.name" :key="item.Id"             
               :label="item.name"></el-option>
        </el-select>
        </el-col>
     <el-col :span="6">
      <el-form-item>
        <el-button type="primary" @click="onSubmit">运行</el-button>       
      </el-form-item>
    </el-col>
  </el-form>

  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  
  watch:{
    excelDate:function(value){
       console.log(value)
     }
  },
  data () {
    return {
      
      form: {
          
          priority1: '',
          priority2: '',
          priority3: '',
          
        },
        prioritys1:this.global.prioritys1,
        prioritys2:this.global.prioritys2,
        prioritys3:this.global.prioritys3,
      
    }
  },
  methods:{
    onSubmit(){

      this.$message('您所计算偏好为1.'+this.form.priority1+'\n'+'2.'+this.form.priority2+'\n'+'3.'+this.form.priority3);
      setTimeout(() => {this.$message({
          message: '恭喜你，运行成功',
          type: 'success'
        });;},3000)

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



