import Vue from 'vue'
import Router from 'vue-router'
import fixTimeLimit from '@/components/fixTimeLimit'
import fixTroubleShootingTime from '@/components/fixTroubleShootingTime'
import upLoad from '@/components/upLoad'
import timeChaets from '@/components/timeChaets'
import timeShow from '@/components/timeShow'
import taskShow from '@/components/taskShow'
import excelEchartShow from '@/components/excelEchartShow'
import runShow from '@/components/runShow'
Vue.use(Router)

export default new Router({
  routes: [
    {path: '/fixTimeLimit', name: 'fixTimeLimit',component: fixTimeLimit},
    {path: '/fixTroubleShootingTime', name: 'fixTroubleShootingTime',component: fixTroubleShootingTime},
    {path: '/upLoad', name: 'upLoad',component: upLoad},
    {path: '/timeChaets', name: 'timeChaets',component: timeChaets},
    {path: '/timeShow', name: 'timeShow',component: timeShow},
    {path: '/taskShow', name: 'taskShow',component: taskShow},
    {path: '/excelEchartShow', name: 'excelEchartShow',component: excelEchartShow},
    {path: '/runShow', name: 'runShow',component: runShow},

  ]
})



