<template>
  <div>
    <div>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-date-picker
            v-model="dateSelect"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-col>
        <el-col :span="6">
          <el-select v-model="dataSelect" placeholder="请选择专业">
            <el-option v-for="Select in dataSelects" :value="Select.select" :key="Select.Id"             
               :label="Select.name"></el-option>
        </el-select>
        </el-col>
        <el-col :span="6">
          <el-button type="primary" @click="onSearch()">查询</el-button>
        </el-col>
      </el-row>

    </div>
    <div ref="timechart" id="timechart" style="width: 800px;height:550px;">
    </div>
   
  </div>
</template>

<script>
import * as echarts from "echarts"
var timeChart=''
export default {
  name: 'timeCharts',
  mounted(){
},

  //props:['timeType','title'],
  



  data () {
    return {
      //timeChart:'',
      dataSelect:"AV", //机械。电子，全部
      dataSelects:this.global.dataSelects,
      dateSelect:['2021-10-22', '2021-10-28'],//日期选择
      countSelect:'time',//工时，数量
      test:'',

      dataResult:[],
      
      bases:[],
      dateList:[],
      timeList:[],
      dateArr:[],
      soure1:[                                                           //echart soure数据
        // ['task', '2021-10-22', '2021-10-23', '2021-10-24', '2021-10-25'],
        // ['厦门', 56.5, 82.1,35,66],
        // ['福州', 51.1, 51.4,23,77],
        // ['长沙', 40.1, 62.2,45,88],
        // ['杭州', 22, 33,44,99],
        // ['泉州', 25.2, 37.1,56,78],
      ],

      value1:'',                                                           
      series1:[],                                                         //echart series1数据
      baseNum:8,
    }
  },

  methods:{

    onSearch(){
      console.log('日期选择',this.dateSelect)
      this.dataResult=this.global.dateExcel.sheet
      if(timeChart==''){
        this.datemation();
        console.log('初始化datemation')
    }
      this.datamation();
      this.setEcharts();
    },

    setEcharts(){
    if(timeChart==''){
      timeChart= echarts.init(this.$refs.timechart)//初始化echarts 
      console.log('初始化echarts')
    }
      
    timeChart.setOption({                                //数据填充

      legend: {},
      tooltip: {
        trigger: 'axis',
        showContent: false
      },
      title: {
          left: 'left',
          text: '总工时'
        },
      dataset: {
        source: this.soure1                           //填充source
      },
      xAxis: { type: 'category' },
      yAxis: { gridIndex: 0 },
      grid: { top: '55%' },
      series: this.series1                             // 填充series


      })


    timeChart.on('updateAxisPointer', function (event) {  //echarts关联联动函数
    const xAxisInfo = event.axesInfo[0];
    if (xAxisInfo) {
      const dimension = xAxisInfo.value + 1;
      timeChart.setOption({
        series: {
          id: 'pie',
          label: {
            formatter: '{b}: {@[' + dimension + ']} ({d}%)'
          },
          encode: {
            value: dimension,
            tooltip: dimension
          }
        }
      });
    }
  });

    },



    
    series2(){
      let series=[]
      
      for(var i=0;i<this.baseNum;i++){                                //折线图填充
        series.push(
          {
          type: 'line',
          smooth: true,
          seriesLayoutBy: 'row',
          emphasis: { focus: 'series' }
        })
      };
      series.push(                                         //扇形图填充
        {
          type: 'pie',
          id: 'pie',
          radius: ['15%', '30%'],
          center: ['50%', '25%'],
          itemStyle: {
          borderRadius: 5,
          borderColor: '#fff',
          borderWidth: 2
        },

          emphasis: {
            focus: 'self'
          },
          label: {
            formatter: '{b}: {@this.value1} ({d}%)'        //value1填充                          
          },
          encode: {
            itemName: 'task',
            value: this.value1,
            tooltip: this.value1
          }
        });    



      return series
    },



    showData(){
        this.dataResult=this.global.dateExcel.sheet
        this.datemation()
        this.datamation();
        this.setEcharts();
      },
      
    datemation(){
      console.log("数据",this.dataResult);
        this.bases=this.global.bases;
      //过滤基地
      this.dataResult=this.dataResult.filter(item=> (item['计划执行部门'] =='航线'))
      for(let i=0;i<this.dataResult.length;i++){
          let  duration=this.dataResult[i]["计划开始日期"]-2
          this.dataResult[i]['计划开始日期']=this.$moment('1900-01-01').add(this.$moment.duration({ 'days': duration })).format('YYYY-MM-DD')
        }
         let t=this.dataResult.filter(item=> (item['计划开始日期'] =='2021-10-26'
                                                  &&item['计划执行地'] =='福州'))
       
        this.test=t 

    },

    datamation(){
      //过滤电子机械      
      if(this.dataSelect=="ME"){
        this.dataResult=this.dataResult.filter(item=> (item['ATA前两位'] !='22'
                                                        &&item['ATA前两位'] !='23'
                                                        &&item['ATA前两位'] !='31'
                                                        &&item['ATA前两位'] !='34'
                                                        &&item['ATA前两位'] !='42'
                                                        &&item['ATA前两位'] !='44'
                                                        &&item['ATA前两位'] !='45'
                                                        &&item['ATA前两位'] !='46'))

            }else if(this.dataSelect=="AV"){
              this.dataResult=this.dataResult.filter(item=> (item['ATA前两位'] =='22'
                                                        ||item['ATA前两位'] =='23'
                                                        ||item['ATA前两位'] =='31'
                                                        ||item['ATA前两位'] =='34'
                                                        ||item['ATA前两位'] =='42'
                                                        ||item['ATA前两位'] =='44'
                                                        ||item['ATA前两位'] =='45'
                                                        ||item['ATA前两位'] =='46'))


            }else if(this.dataSelect=="Total"){}

        
        //日期列表处理
       
        var dateLi=[]
        console.log("数据条目",this.dataResult.length); 
        for(let i=0;i<this.dataResult.length;i++){ 
          let d=this.$moment(this.dataResult[i]["计划开始日期"]).format('YYYY-MM-DD')      
          //筛选日期    
          if(d>=this.dateSelect[0]&&d<=this.dateSelect[1]){
            dateLi.push((d)) 
          }
                   
        };
        dateLi=Array.from(new Set(dateLi));
        
        dateLi=dateLi.sort();
        console.log('日期',dateLi)
        //工时列表处理
        
        let taskList=[];
        for(let i=0;i<this.global.bases.length;i++){          
          taskList[i]=[];          
          taskList[i]=[this.global.bases[i]["base"]] 
          var b=taskList[i] 
          //筛选日期和基地

          for(let j=0;j<dateLi.length;j++){
                let dataArr=this.dataResult.filter(item=> (item['计划开始日期'] ==dateLi[j]
                                                            &&item['计划执行地'] ==this.global.bases[i]["base"]))
               
                                                           

                if(this.countSelect=='time'){//总工时
                  var countTime=0
                  for(let k=0;k<dataArr.length;k++){
                    countTime+=dataArr[k]['计划工时']
                  }

                  // if(this.dataSelect=="Total"){
                  //   if(this.global.bases[i]["base"]=='厦门'){
                  //     let c= this.global.troubleShootingTime.filter(item=> (item['date'] ==dateLi[j]))
                  //     countTime+=c["xiamenTime"]}
                  //     else if(this.global.bases[i]["base"]=='福州'){
                  //     let c= this.global.troubleShootingTime.filter(item=> (item['date'] ==dateLi[j]))
                  //     countTime+=c["fuzhouTime"]}
                  // }

                }

                b.push(countTime.toFixed(2))
                

                taskList[i]=b  

          }
               
        
        }

        dateLi.unshift('task') 
        
        let c=[]
        c.push(dateLi)
        
        for(let i=0;i<taskList.length;i++){
          c.push(taskList[i])
        }       
        this.soure1= JSON.parse(JSON.stringify(c))
        
        this.value1=this.soure1[0][1]                           //value1赋值
        
        this.series1=this.series2() 
        this.baseNum =8   
      }, 




  },
  mounted(){
    if(this.global.dateExcel==''){
      this.$message({
          message: '请先上传数据！',
          type: 'warning'
        });
    }
    else{
      this.showData()
    }
    
  },

  computed:{
    
  },
}
</script>

