<template>
  <div class="hello">
    <h1>upLoad</h1>

    <!-- <el-upload
      class="upload-demo"
      drag
      action :limit="1"
      :http-request="getExcel"
      multiple>
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
      <div class="el-upload__tip" slot="tip">只能上传excel文件，且不超过500kb</div>
    </el-upload> -->

    <el-upload class="upload-demo" 
        action :limit="1"
        :http-request="getExcel"
        accept=".csv"
        >
          <el-button size="small" type="primary">点击上传</el-button>
      </el-upload>



  </div>
</template>

<script>
import XLSX from 'xlsx';
export default {
  name: 'upLoad',
  data () {
    return {
      
    }
  },
  methods:{


    //解析excel   
    analysis(file) {
    return new Promise(function (resolve, reject) {
      const reader = new FileReader();
      reader.onload = function (e) {
        const data = e.target.result;
      
        let datajson = XLSX.read(data, {
          type: "binary",
          
        });


        const result = [];
        datajson.SheetNames.forEach(sheetName => {
          result.push({
            sheetName: sheetName,
            sheet: XLSX.utils.sheet_to_json(datajson.Sheets[sheetName])
          });
        });
        resolve(result);
      };
      reader.readAsBinaryString(file.file);
    });
    },


    // 导入文件
    getExcel(file) {
    const types = file.file.name.split(".")[1]; //获取文件后缀
    const fileType = ["xls","csv","xlsx"].some(
      item => item == types
    );
    if (!fileType) {
      this.$message("格式错误！请重新选择,文件名中不要带“.");
      return;
    }
    this.analysis(file).then(tableJson => {
      if (tableJson && tableJson.length > 0) {
        //成功解析出数据
        let dataExcel = tableJson[0];
       // console.log("数据",dataExcel);
        //console.log(JSON.stringify(dataExcel.sheet));
        this.global.dataExcel=dataExcel
        console.log("数据",this.global.dataExcel);
      }
    });
    },





  }
}
</script>

