//格式化日期
export const formatExcelDate = (num) => {
    let duration = num - 1
    // 1900/2/29的num为60
    if (num > 60) {
    // 对于num大于60的需解析日期,要减去多的1900/2/29日的那一天
    duration = num - 2
    }
    return moment(START_TIME).add(moment.duration({ 'days': duration })).format(FORMAT)
   }



