<template>
  <div id="app" class="login">
    <el-container>
      
      <el-header>
        <el-col :span="8">
          <!-- 导航菜单 -->
          <el-menu :router="true" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            
            <el-submenu index="1">
              <template slot="title">图表展示</template>
              <!-- <el-menu-item index="/timeShow">工时展示</el-menu-item>
              <el-menu-item index="/taskShow">任务数展示</el-menu-item> -->
              <el-menu-item index="/excelEchartShow">工时汇总</el-menu-item>
            </el-submenu>

            <el-menu-item index="/runShow">派工展示</el-menu-item>
            <el-submenu index="2">
              <template slot="title">基础设置</template>
              <el-menu-item index="/fixTimeLimit">工时设置</el-menu-item>
              <el-menu-item index="/fixTroubleShootingTime">排故工时设置</el-menu-item>
              <el-menu-item index="/upLoad">文件上传</el-menu-item>
            </el-submenu>
        
          </el-menu>
       </el-col>
       <el-col :span="8" :offset="6">



        <el-upload class="upload-demo" 
          action :limit="1"
          :http-request="getExcel"
          accept=".xlsx"
          >
            <el-button size="small" type="primary">点击上传</el-button>
        </el-upload>
      </el-col>


      </el-header>
      <el-main >
        <router-view/>
      </el-main>
    </el-container>
   
    
  </div>
</template>

<script>
import XLSX from 'xlsx';
export default {
  name: 'App',
  data() {
      return {
        activeIndex: this.$route.path,//导航显示激活的为当前路径
        dataExcel1:[]
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        // console.log(key, keyPath);
      },




      //解析excel   
    analysis(file) {
    return new Promise(function (resolve, reject) {
      const reader = new FileReader();
      reader.onload = function (e) {
        const data = e.target.result;
      
        let datajson = XLSX.read(data, {
          type: "binary",

          
        });


        const result = [];
        datajson.SheetNames.forEach(sheetName => {
          result.push({
            sheetName: sheetName,
            sheet: XLSX.utils.sheet_to_json(datajson.Sheets[sheetName])
          });
        });
        resolve(result);
      };
      reader.readAsBinaryString(file.file);
    });
    },


    // 导入文件
    getExcel(file) {
    const types = file.file.name.split(".")[1]; //获取文件后缀
    const fileType = ["xls","csv","xlsx"].some(
      item => item == types
    );
    if (!fileType) {
      this.$message("格式错误！请重新选择,文件名中不要带“.");
      return;
    }
    this.analysis(file).then(tableJson => {
      if (tableJson && tableJson.length > 0) {
        //成功解析出数据
        let dataExcel = tableJson[0];
       // console.log("数据",dataExcel);
        //console.log(JSON.stringify(dataExcel.sheet));
        this.global.dateExcel=dataExcel
        
        //console.log("数据",this.global.dateExcel);
      }
    });
    },





















    }

}
</script>

<style>
    
 

</style>



